.App {
  text-align: center;
  max-width: 500px;
  margin: auto;
}

.guess {
  background: #eee;
  border-radius: 5px;
  margin: 10px 0px;
  padding: 5px 0px;
}

.guess.incorrect {
  background: #e67e22;
}

.guess.correct {
  background: #2ecc71;
}

.guesser input[type=text] {
  width: 75%;
  padding: 5px;
  margin-right: 3px;
}

.guesser input[type=button] {
  width: 20%;
  padding: 5px;
}

.guess-container {
  position: relative;
}

.guess-search {
  background: white;
  max-height: 300px;
  overflow: scroll;
  position: absolute;
  bottom: 30px;
  width: 75%;
  border: 1px solid #ccc;
  drop-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

.guess-search.hidden {
  display: none;
}

.guess-search ul li {
  list-style-type: none;
  padding: 5px;
  margin: 0px;
}

.guess-search ul {
  text-align: left;
  margin: 0px;
  padding: 10px 0px;
}